/* jetbrains-mono-regular - latin_cyrillic */
@font-face {
  font-family: 'Jet Brains Mono';
  font-style: normal;
  font-weight: 400;
  src: url('./fonts/jetbrains-mono-v6-latin_cyrillic-regular.woff2') format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
       url('./fonts//jetbrains-mono-v6-latin_cyrillic-regular.woff') format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}
/* jetbrains-mono-700 - latin_cyrillic */
@font-face {
  font-family: 'Jet Brains Mono';
  font-style: normal;
  font-weight: 700;
  src: url('./fonts/jetbrains-mono-v6-latin_cyrillic-700.woff2') format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
       url('./fonts/jetbrains-mono-v6-latin_cyrillic-700.woff') format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

